.btn{
    padding: 8px 18px;
    border-radius: 20px;
    font-size: 13px;
    font-weight: 500;
    text-transform: uppercase;
    @include transition();
}

.btn-primary {
    min-height: 40px;
    background-color: $color-primary;
    border: 1px solid transparent;
    color: #fff;
    @include transition();

    &:hover {
        background-color: darken($color-primary, 10);
    }
}

.btn-white {
    background-color: #fff;
    box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.342);
    color: $color-primary !important;
    padding: 10px 16px;

    &:hover {
        @include scale(1.03);
        background-color: rgb(248, 248, 248);
        box-shadow: 1px 1px 7px rgba(0, 0, 0, 0.392);
    }

    &--outline {
        background-color: transparent;
        border: 2px solid #fff;
        color: #fff;

        span {
            font-size: 10px;
        }

        &:hover {
            color: black;
            background-color: #fff;
        }
    }
}

.btn-transparent {
    background: none !important;
    border: none;
    outline: none;
    font-weight: bold;
    cursor: pointer;

    &:active,
    &:focus {
        border: none;
        outline: none;
    }
}